'use client';

import { useState } from 'react';
import Link from 'next/link';

export default function Home() {
  const [searchSpecialization, setSearchSpecialization] = useState('');

  return (
    <div className="max-w-7xl mx-auto px-4 py-12">
      {/* Hero Section */}
      <section className="bg-gradient-to-r from-blue-500 to-blue-600 text-white rounded-lg p-8 mb-12">
        <h1 className="text-4xl font-bold mb-4">Welcome to Clinics Booking System</h1>
        <p className="text-xl mb-6">
          Find and book appointments with your favorite doctors at trusted clinics
        </p>

        {/* Search Bar */}
        <div className="bg-white rounded-lg p-6 shadow-lg">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <input
              type="text"
              placeholder="Search by specialization..."
              value={searchSpecialization}
              onChange={(e) => setSearchSpecialization(e.target.value)}
              className="px-4 py-2 border border-gray-300 rounded text-gray-800"
            />
            <input
              type="text"
              placeholder="Search by city..."
              className="px-4 py-2 border border-gray-300 rounded text-gray-800"
            />
            <button className="bg-blue-600 text-white px-6 py-2 rounded hover:bg-blue-700">
              Search
            </button>
          </div>
        </div>
      </section>

      {/* Quick Actions */}
      <section className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-12">
        <Link href="/doctors">
          <div className="bg-white rounded-lg shadow p-6 hover:shadow-lg cursor-pointer">
            <h3 className="text-xl font-bold mb-2">Find a Doctor</h3>
            <p className="text-gray-600">Browse our network of qualified doctors</p>
          </div>
        </Link>

        <Link href="/clinics">
          <div className="bg-white rounded-lg shadow p-6 hover:shadow-lg cursor-pointer">
            <h3 className="text-xl font-bold mb-2">Find a Clinic</h3>
            <p className="text-gray-600">Discover clinics near you</p>
          </div>
        </Link>

        <Link href="/login">
          <div className="bg-white rounded-lg shadow p-6 hover:shadow-lg cursor-pointer">
            <h3 className="text-xl font-bold mb-2">Login</h3>
            <p className="text-gray-600">Access your account</p>
          </div>
        </Link>
      </section>

      {/* Features */}
      <section className="bg-white rounded-lg shadow p-8">
        <h2 className="text-3xl font-bold mb-6">Why Choose Us?</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <h3 className="text-lg font-bold mb-2">Easy Booking</h3>
            <p className="text-gray-600">
              Book appointments in just a few clicks with our user-friendly interface
            </p>
          </div>
          <div>
            <h3 className="text-lg font-bold mb-2">Verified Doctors</h3>
            <p className="text-gray-600">
              All doctors are verified and qualified professionals
            </p>
          </div>
          <div>
            <h3 className="text-lg font-bold mb-2">24/7 Support</h3>
            <p className="text-gray-600">
              Our customer support team is always ready to help
            </p>
          </div>
          <div>
            <h3 className="text-lg font-bold mb-2">Secure Payments</h3>
            <p className="text-gray-600">
              Your payments are safe and secure with us
            </p>
          </div>
        </div>
      </section>
    </div>
  );
}
